import { get, post } from '../../libs/request.js';
export default {
	data() {
		return {
			joinData: {
				shopName:"",
				name:"",
				phone:"",
				remarks:""
			}
		};
	},
	methods: {
		joinUs() {
			post('/user/joinus', this.joinData).then(res => {
				if (res.code === 200) {
					uni.showToast({
						title:"信息提交成功",
						icon:"none"
					})
				} else {
					uni.showToast({
						title:res.msg,
						icon:"none"
					})
				}
			});
		}
	}
};
